@import "colors";
@import "mixins";

.md-modal {
  position: fixed;
  top: 200px;
  left: 50%;
  width: 85%;
  height: auto;
  z-index: 2000;
  visibility: hidden;
  backface-visibility: hidden;
  transform: translateX(-50%);
  perspective: 1300px; }

.md-show {
  visibility: visible; }

.md-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  visibility: hidden;
  top: 0;
  left: 0;
  z-index: 1000;
  opacity: 0;
  background: #f4f5f6;
  -webkit-transition: all 0.3s;
  -moz-transition: all 0.3s;
  @include transition(all 0.3s); }

.md-show ~ .md-overlay {
  opacity: 1;
  visibility: visible; }

.md-content {
  background: #fff;
  position: relative;
  border-radius: 3px;
  margin: 0 auto;
  padding: 30px;
  transform-style: preserve-3d;
  transform: translateZ(100px) translateX(-30%) rotateY(90deg);
  transform-origin: 0 100%;
  opacity: 0;
  @include transition(all 0.3s); }

.md-show .md-content {
  transform: translateZ(0px) translateX(0%) rotateY(0deg);
  opacity: 1; }

.md-close {
  position: absolute;
  right: 15px;
  top: 15px;
  border: 0;
  outline: 0;
  background: url('../img/close.png');
  background-size: contain;
  width: 30px;
  height: 30px;
  cursor: pointer; }
